* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 1200px;
  margin: 0 auto;
}

h2 {
  color: #1864ab;
  margin-top: 20px;
  margin-bottom: 30px;
  padding-left: 40px;
  font-family: Nunito, sans-serif;
  font-size: 36px;
  font-weight: 800;
}

.hidden {
  visibility: hidden;
  opacity: 0;
}

section {
  margin-bottom: 50px;
}

header {
  background-color: #1864ab;
  grid-template-columns: 3fr 1fr;
  justify-content: center;
  align-items: center;
  height: 200px;
  margin-top: 30px;
  margin-bottom: 50px;
  display: grid;
}

h1 {
  letter-spacing: 8px;
  color: #fff;
  justify-self: start;
  padding-left: 20px;
  font-family: Nunito, sans-serif;
  font-size: 62px;
  font-style: italic;
  font-weight: 800;
  line-height: 1.2;
  text-decoration: underline;
}

.calculator-image {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  justify-self: end;
  width: 200px;
  height: 200px;
}

.instructions {
  padding-left: 40px;
  font-family: Mako, sans-serif;
  font-size: 24px;
}

.instructions-list {
  list-style: square;
}

.instructions-list li {
  margin-bottom: 10px;
  margin-left: 30px;
}

.calculate {
  grid-template-columns: 1fr;
  display: grid;
}

.calculate-title {
  color: #fff;
}

.input-form {
  color: #fff;
  background-color: #1864abe6;
  border-radius: 10px;
  padding-left: 40px;
  box-shadow: 10px 10px 10px #0003;
}

.input-controls {
  grid-template-columns: repeat(2, 1fr);
  justify-content: start;
  gap: 20px;
  font-family: Mako, sans-serif;
  font-size: 24px;
  display: grid;
}

input {
  text-align: center;
  background-color: #e9ecef;
  border-radius: 5px;
  justify-self: end;
  width: 120px;
  height: 35px;
  margin-right: 30px;
  font-size: 20px;
}

button {
  color: #000;
  letter-spacing: 1px;
  background-color: #37b24d;
  border-radius: 5px;
  grid-area: 3 / 2;
  justify-self: end;
  width: 120px;
  height: 40px;
  margin-bottom: 30px;
  margin-right: 30px;
  font-size: 20px;
  font-weight: 300;
}

p {
  margin-bottom: 20px;
}

.results {
  padding-left: 40px;
  font-family: Mako, sans-serif;
  font-size: 24px;
  line-height: 1.5;
}
